import React, { Component } from 'react';
import PropTypes from 'prop-types';

/*
* Компонент для рендера полного перечня рецептов
* На вход принимает коллекцию с данными из datalist.js спускаемую пропсами от родительских компонент
* @input dataitems - коллекция с рецептами и полной информацией (получаем в родительском компоненте)
* Это конечный компонент, не имеет дочерних компонент
*/

export default class MainBlockRecipeContent extends Component {
  constructor(props) {
    super(props);
    this.unfolding = this.unfolding.bind(this);
  }

  unfolding(el) {
    let element = '.';
    el.target.classList.forEach(item=> {
      if(item.includes('headingEl')) element = element+item;
    });
    const symbolType = document.querySelector(element);
    const container = document.querySelector('#ingredients__'+element.replace('.headingEl', ''));
    
    container.classList.toggle('inactively');
    symbolType.classList.toggle('symboldown');
    symbolType.classList.toggle('symbolup');
  }
  
  render() {
    return (
      <div className='selection__content'>
        <div className="selection__recipes">
          <ul className='recipes__list'>
          {

            this.props.dataitems.map((value, index)=>{
              return (
                <li key={value.labelname}>
                  <h3>{`${value.labelname}`}</h3>
                  <img src={value.img} alt={`Изображение: ${value.labelname}`} className='img__recipe'></img>
                  <div className='ingredients__information'>{value.information}</div>
                  <div className='ingredients__wrapper'>
                    <span className={`ingredients__heading symboldown ${'headingEl'+index}`} onClick={this.unfolding}>{`Ингредиенты`} </span>
                    <ul className='ingredients__container inactively' id={'ingredients__' + index}>{value.ingridient.map((value, index) =>{
                      return <li key={index}>{value}</li>
                    })}</ul>
                  </div>
                </li>
                
              )
            })

          }
          </ul>
        </div>
      </div>
    )
  }
};


MainBlockRecipeContent.propTypes = {
  dataitems: PropTypes.arrayOf(PropTypes.object).isRequired,
};


